


































export default {
	name: 'searchView',
	components: {
		//组件模板
	},
	props: {
		//组件道具（参数）
		/* ****属性用法*****
		 *
		 * 传递类型 type: Array | Number | String | Object
		 * 为必传 required: true
		 * 默认值 default: ''
		 *
		 */
	},
	data() {
		//数据
		return {
			searchSession:[],//历史纪录
			foodList:['蛋黄酥','烧肉粽','牛奶糖','三只松鼠坚果大礼包','百草味猪肉干','鳄鱼生鲜','糖'],
			keyCode: ''
		};
	},
	onReachBottom() {
		uni.showToast({ title: '触发上拉加载' });
	},
	methods: {
		//方法
		search(code) {
			if(code!==''){
				this.keyCode = code;
				uni.showToast({ title: '加载中...',icon:'loading'});
				setTimeout(() =>{
					uni.hideLoading();
					uni.navigateTo({
						url: '../../goodPage/goods/goods-list?cid='+Math.random()+'&&name='+code
					});
					this.searchSession.push(code);
					let d = Array.from(new Set(this.searchSession));//去重
					uni.setStorage({
						key: 'searchSession',
						data: d,
						success: () =>{
							console.log('success');
						}
					});
				}, 2000);
			}

		},
		toGoods(goods){
			uni.navigateTo({
				url: '../../goodPage/goods/goods?cid='+goods.goods_id+'&&name='+goods.name+'&&img='+goods.img+'&&price='+goods.price
			});
		},
		clearSession(){
			this.searchSession = [];
			uni.removeStorage({
				key: 'searchSession',
				success:(res) =>{
					console.log('success');
				}
			});
		}
	},
	computed: {
		//计算属性
	},
	watch: {
		//监测数据变化
	},

	//===================组件钩子===========================

	created() {
		//实例被创建完毕之后执行
	},
	mounted() {
		uni.getStorage({
			key: 'searchSession',
			success: (res) =>{
				this.searchSession = res.data;
			}
		});
	}
};
